<template>
  <view> 
	  <CloseHead title=""></CloseHead>
	  <view class="label-contain">
		  <view class="classifyBigBox" >
			  <navigator class="label-tag-select" 
			             v-for="(item,index) in labels" :key="index"
			             url="/src/pages/level2/message/releaseLabel"> 
				  <view class="place">
				  <text class="select-distance">#</text>				  
				  <text class="text-label" >{{item}}</text>
				  </view>
			  </navigator>
		
		  <view class="label-tag-release" >
		  		<text style="padding: 10%;" @tap="clickToPost">发布</text>	  
		  </view>  
		  </view>
		  <!-- <view class="label-tag-release" >
		  		<text style="padding: 10%;" @tap="clickToPost">发布</text>	  
		  </view> -->
	  </view>
	  
	  <view class="uni-textarea">
	       <textarea 
				class="type-input" 
				maxlength="400" 
				placeholder="分享你的快乐"
				v-model="postContent"				
				/>
	  </view>
	  
	  <view class="body-view">
	  	<view class="uploads">		
	  	<view class='upload-image-view'>			
	  		<block v-for="(image,index) in imageList" :key="index">
	  			<view class='image-view'>
	  				<image :src="image" :data-src="image" @tap="previewImage"></image>
	  				<view class='del-btn' :data-index="index" @tap='deleteImage'>
	  					<view class='baicha'></view>
	  				</view>
	  			</view>
	  		</block> 
	  		<view class='add-view' v-if="imageList.length < imageLength" @tap="chooseImage">
	        <!-- 十字架 -->
	        			<view class="cross">
	        				<view class="transverse-line"></view>
	        				<view class="vertical-line"></view>
	        			</view>
	         
	        		</view>
	        		<!-- <view class='info'>上传证书/证件，不超过{{imageLength}}张。(非必填)</view> -->
	        	</view>
	        	</view>
  </view>
  </view>
</template>

<script>
import CloseHead from '../../../components/message/CloseHead.vue'
import{
		postArticle,
		setPhoto,
	}from "../../../axios/message/message.js";
	
export default {
	data(){
		return{
			imageList: [], //保存图片路径集合
			images:[],
			imageLists:[],
			imageLength: 4, //限制图片张数
			sourceTypeIndex: 2, //添加方式限制
			sizeTypeIndex: 2, //图片尺寸限制
			postPhotoState:0,
						
			articleNum:0,
			postState:0,
			itemList:[],
			labelList:[],
			labels:[],
			postContent:"",
			art:{
				"content":this.postContent
				// "content":"",
			}
		}
			},
	onShow(){
		
		this.labels.push(
		    '选择标签',// uni.getStorageSync('labels'),
		 );	
		console.log(this.labels);
		if(uni.getStorageSync('labels')!=""){
		this.labels=uni.getStorageSync('labels');
		// this.labels = this.labels.split(",");
		console.log(this.labels);
		}
		// if(this.labels==""){
		// 	this.labels.push(
		// 	     // '运动', 
		// 	); 
		// }
		
		// uni.removeStorageSync('labels');
		
		// console.log(this.labels);
		// if(e.label!=null){
		    
		// 	// this.labels=JSON.parse(e.label);
		// 	console.log(this.labels);
		// }
	},
    components: {
	  CloseHead
   },
    methods:{
	  postArticle(articleInfo,labels){
	  	return postArticle({articleInfo,labels}).then(
	  	(res) => {
			this.itemList =res.data;
			this.articleNum=this.itemList.data;
			this.postState=this.itemList.state;
	        console.log(this.articleNum);
			console.log(this.postState);
			this.getUploadFile();
	  		},
	  	(err) => {
	  	  console.log(err);
	  	}
	  	);
	  },
	  
	  chooseImage(){
		 
		  uni.chooseImage({
		  	sourceType: ['camera', 'album'],	  	
		  	sizeType: ['compressed', 'original'],	  	
		  	// count: this.imageLength - this.imageList.length,
		  	count:4,
		  	success: (res) => {
				console.log(res);
		  		this.imageList = this.imageList.concat(res.tempFilePaths);
				this.imageLists=res.tempFilePaths;
				this.images=res.tempFiles;
				// this.list =res.data;			
				// this.postPhotoState=this.list.state;
				// console.log(this.postPhotoState);
		  	}
		  })
	  },
	  getUploadFile(){ 
		  let files=[];
		  for(let i=0;i<this.images.length;i++){
		  	files[i]={
		  		name:i===0?"thumbnail":""+i,
		  		file:this.images[i],
		  		uri:this.imageLists[i]
		  	}
		  }
		  console.log(files);	  
		  console.log(this.articleNum);
		  setPhoto(this.articleNum,files);
		  if(this.postState==200){
		     	setTimeout(function(){
		     	uni.showToast({
		     		title: '发布成功',
		     		icon:'none',
		     		duration: 2000
		     		  	})
		     		 },1000);
		     }
		   else{
		        setTimeout(function(){
		     	uni.showToast({
		     		title: '发布失败',
		     		icon:'none',
		     		duration: 2000
		     		  		})
		     		},1000);
		     }
			 //清除缓存
			 uni.removeStorageSync('labels');
	  },
	  // setPhoto(articleId,files){
	  // 	return setPhoto({articleId,files}).then(
	  // 	(res) => {
	  //      //     console.log(res.data.state);
	  // 		   // this.postPhotoState=res.data.state;
			//    // var obj = JSON.parse(res.data);
			//    // this.postPhotoState=obj.state;
			//   //  console.log(res.data);
			//   //  console.log(res.data.state);
			// 		// if(this.postState==200&&this.postPhotoState==200){
			//   //  		  			  setTimeout(function(){
			//   //  		  			  uni.showToast({
			//   //  		  			  	title: '发布成功',
			//   //  		  			  	icon:'none',
			//   //  		  			  	duration: 2000
			//   //  		  			  })
			//   //  		  			  },1000);
			//   //  }
			//   //       else{
			//   //  		  			  setTimeout(function(){
			//   //  		  			  uni.showToast({
			//   //  		  			  	title: '发布失败',
			//   //  		  			  	icon:'none',
			//   //  		  			  	duration: 2000
			//   //  		  			  })
			//   //  		  			  },1000);
			//   //  }
	  // 		},
	  // 	(err) => {
	  // 	  console.log(err);
	  // 	}
	  // 	);
	  // },
	  //预览图片
	  previewImage: function(e) {
	  	var current = e.target.dataset.src
	  	uni.previewImage({
	  		current: current,
	  		urls: this.imageList
	  	})
	  }, 
	  //删除图片
	  deleteImage: function(e) {
	  	var index = e.target.dataset.index;
	  	var that = this;
	  	var images = that.imageList;
	  	images.splice(index, 1);
	  	that.imageList = images;
	  },	  
	  clickToPost(){
		  let length=4;
		  for(let i = 1; i < length; i++){
			  this.labelList.push({
				  labelId: i,
				  });
		  }
		  this.art.content=this.postContent;
		//   this.postArticle(this.art,this.labelList);
		  // console.log(this.postPhotoState);
		  // this.getUploadFile();
          if(this.art.content!=""){
		   this.postArticle(this.art,this.labelList);
		   
		   uni.switchTab({
		  			  	url:"../square/square"
		  			  });
		   uni.removeStorageSync('labels');
		   }
		   else{
		  			  uni.showToast({
		  			  	title: '请填写发布内容',
		  			  	icon:'none',
		  			  	duration: 2000
		  			  });	
		   }
	  }
  }
};
</script>

<style scoped>
@import url("../../../../static/icons/iconfont/iconfont.css"); 
/* 标签容纳区 */
.label-contain{
	width: 100%;
	height: 95rpx;
	margin-top: 20px;
	background-color: #FDF7EF ;
}
/* 标签样式 */
/* .label-tag{
	display: flex;
	margin-left: 10px;
	padding: 2%;
	flex-wrap: wrap;
} */
.classifyBigBox {
	/* width: 95%; */
	display: flex;
	flex-direction: raw;
	padding: 20rpx;
	flex-wrap: wrap;
	position: relative;
}
/* 选择标签按钮 */
.label-tag-select{
	/* padding: 1.4%;
	width: 130rpx;
	background-color: #FFEBC2;
	border-radius: 15px;
	font-size: 14px;
	margin-right: 63%; */
	padding: 1.8%;
	margin-left: 10rpx;
	margin-right: 10rpx;
	margin-bottom: 20rpx;
	height: 30rpx;
	background-color: #FFEBC2;
	border-radius: 40rpx;
	color: #696969;	
	font-size: 27rpx;
}
.place{
	position: relative;
	/* left: -2rpx; */
	top: -3rpx;
}
.text-label{
	margin-left: 8rpx;
	margin-right: 10rpx;
}
/* 发布按钮 */
.label-tag-release{
	/* margin-left: 10px; */
	margin-top: 5rpx;
	width: 95rpx;
	height: 50rpx;
	line-height: 48rpx;
	text-align: center;
	background-color: #EBA284;
	border-radius: 15px;
	font-size: 14px;
	color: white;
	position: absolute;
	right: 30rpx;
}
/* #间距控制 */
.select-distance{
	margin-left: 8px;
	/* margin-right: 8px; */
}
/* 文本框样式 */
.uni-textarea{
	margin-top: 12px;
	margin-left: 20px;
	margin-right: 20px;
} 
.type-input{
	width: 100%;
	font-size: 18px;
	height: 340rpx;
	/* font-family:; */	
}

.top{
	margin-top: 20px;	
}
/* 图片展示样式 */
.photo{
	margin-left: 20px;
	margin-right: 20px;
	margin-top: 3px;
	height: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}
.photo-distance{
	margin-right: 3px;
}
/* 选择图片区域 */
.body-view {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	}
.upload-image-view {
	width: 100%;
	margin: 20upx 0 20upx 0;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	}
.upload-image-view .image-view {
	height: 130upx;
	width: 130upx;
	position: relative;
	margin: 15upx 15upx 15upx 0upx;
	border-radius: 8upx;
	}	 
.upload-image-view .image-view image {
	height: 100%;
	width: 100%;
	border-radius: 8upx;
	}
.upload-image-view .image-view .del-btn {
	background-color: #f67371;
	border-radius: 50%;
	width: 25upx;
	height: 25upx;
	position: absolute;
	top: -12upx;
	right: -12upx;
	z-index: 2;
	display: flex;
	justify-content: center;
	align-items: center;
	}	 
.upload-image-view .image-view .del-btn .baicha {
	display: inline-block;
	width: 20upx;
	height: 5upx;
	background: #fff;
	line-height: 0;
	font-size: 0;
	vertical-align: middle;
	-webkit-transform: rotate(45deg);
	}
	 
.upload-image-view .image-view .del-btn .baicha:after {
	content: '/';
	display: block;
	width: 20upx;
	height: 5upx;
	background: #fff;
	-webkit-transform: rotate(-90deg);
	}
	 
.upload-image-view .add-view {
	height: 115upx;
	width: 115upx;
	margin: 15upx 15upx 15upx 0upx;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(255, 255, 255, 0.00);
	border: 3upx dashed #979797;
	border-radius: 8upx;
	}
.upload-image-view .add-view .cross {
	height: 48upx;
	width: 48upx;
	display: flex;
	flex-wrap: wrap;
	position: relative;
	}
.upload-image-view .add-view .cross .transverse-line {
    height: 100%;
    width: 48%;
    position: absolute;
    border-right: 3upx solid #5A5A5A;
    top: 0;
    left: 0;
    }    
.upload-image-view .add-view .cross .vertical-line {
    height: 48%;
    width: 100%;
    position: absolute;
    border-bottom: 3upx solid #5A5A5A;
    top: 0;
    left: 0;
    }	
.uploads{
	width: 92%;
	}
</style>
